async function insertOrder(info) {
  let params = {
    info,
  };
  let data = {};
  await fetch(`https://cors-anywhere.herokuapp.com/http://37.148.212.44:8080/insertorder`, {
    method: 'POST',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      params,
    }),
  }).then(response => response.json()).then((responseJson) => {
    data = responseJson;
    console.log('data', data);
  },
      ).catch((err) => {
        console.log('Fetch Err', err);
      });
  return data;
}

async function insertMenu(title, description, subdescription, type, price, gramaj) {
  let params = {
    title,
    description,
    subdescription,
    type,
    price,
    gramaj,
  };
  let data = {};
  await fetch(`https://cors-anywhere.herokuapp.com/http://37.148.212.44:8080/insertmenu`, {
    method: 'POST',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      params,
    }),
  }).then(response => response.json()).then((responseJson) => {
    data = responseJson;
    console.log('data', data);
  },
      ).catch((err) => {
        console.log('Fetch Err', err);
      });
  return data;
}

function calculateOrderAmount(array) {
  let result = 0;
  array.map((item) => (
    result = result + (parseInt(item.price) * item.amount)
  ));
  return String(result);
}

// Hook
function useWindowSize() {
  const isClient = typeof window === 'object';

  function getSize() {
    return {
      width: isClient ? window.innerWidth : undefined,
      height: isClient ? window.innerHeight : undefined
    };
  }

  const [windowSize, setWindowSize] = useState(getSize);

  useEffect(() => {
    if (!isClient) {
      return false;
    }
    
    function handleResize() {
      setWindowSize(getSize());
    }

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []); // Empty array ensures that effect is only run on mount and unmount

  return windowSize;
}

